.background {
    position: absolute;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    z-index: -1;
}


.card {
    position: relative;
    z-index: 100;
    height: 70vh;
    
}

.card:hover {
    transform:  rotateY(-180deg);
}

.wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: rgba(0, 0, 0, 2);
}

@media screen and (max-width:768px) {
    .card{
        height: 50vh;
    }
}